import React, { useState } from "react";
import Phonom from "./Phonom.svg";
import Pho from "./Pho.png";
import ShoppingCartIcon from "@material-ui/icons/ShoppingCart";
import {
	BrowserRouter as Router,
	Switch,
	Route,
	Link
  } from "react-router-dom";

function Navbar() {
	const [menuOpen, setMenuOpen] = useState(true);

	return (
		<>
		

			<div className="Heo">
				<section className="navbar">
					<img src={Phonom} alt="Logo" style={{ height: "23vh" }} />

					<ul>
						<li><Link to="/" activeClassName="hurray">Home</Link></li>
                        <li><Link to="/Order" activeClassName="hurray">Order</Link></li>			
			    		<li><Link to="/Contact" activeClassName="hurray">About</Link></li>

           			 <ShoppingCartIcon />
					</ul> 
				
				</section>
				<div className={menuOpen ? 'menu-btn' :'menu-btnOpen'  } onClick={() => setMenuOpen(!menuOpen)}>
     				<div className = 'menu-btn__burger'/>		  
   				 </div>

				<section className="mobileMenu" style={{ display: menuOpen ? 'none' : 'flex'}}>
				<div className={menuOpen ? 'menu-btn' :'menu-btnOpen'  }
                 onClick={() => setMenuOpen(!menuOpen)}>
     				   <div className = 'menu-btn__burger'/>		  
   				</div>
				<img src={Phonom} alt="Logo" style={{ height: "23vh" }} />
				<ul>
					<li>Home	</li>
					<li>About</li>
					<li>Order</li>
					<li>Contact Us</li>

           			 <ShoppingCartIcon />
				</ul>
				
				</section>
			</div>
		</>
	);
}

export default Navbar;
