import React from 'react'
import hero2 from './hero2.png'

function Contact() {

    return (

        <div className="menuModal2" style={{top:'20vh'}}>
          <section classname="about"> 

            <h1>About Us</h1>
            
              <section className="pictrio">
                
              </section>
          
          
          </section>



           <h1>Reach us at 555-5555</h1>
        
           <form 
           
          className="contact__form">
            <input 
            
          
              type="text" name="name" 
              placeholder="name" 
              title="Your name" 
              maxLength="50" 
              required/>
            <input 
   
              type="email" name="email" 
              placeholder="email" 
              title="Your email" 
              maxLength="50" 
              required/>
            <textarea 
   
              type="text" name="message" 
              placeholder="message" 
              title="Your message" 
              maxLength="550" 
              required/>
            <input 
              type="submit" 
              value="send message"/>
        </form> 

        </div>
    )
}

export default Contact
